export const articleInnerHtml = {
    worklist:[
        {
            articleId: 1,
            content:`<p style="text-align: left;"><span style="font-size: 29px;"><strong>工作规程</strong></span></p><p style="text-align: left;"><br></p><p><em>商标品牌指导站工作规程</em></p><p> </p><p><br></p><p><strong>第一章 &nbsp;总 &nbsp;则</strong></p><p> </p><p><br></p><p style="text-indent: 2em;">第一条 &nbsp; 本规程所述商标品牌指导站（以下简称指导站）是指由广州商标审查协作中心（以下简称商标中心）按规划自主设立或和其他各类知识产权公益性事业单位、行业协会、高校院所、服务机构等单位合作共建，旨在为市场主体和社会公众提供便捷、高效、优质的知识产权公共服务。接受广东省知识产权保护中心（以下简称省保护中心）业务指导和检查，由商标中心或与共建单位共同负责业务推进和日常运营、管理。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第二条 &nbsp; 指导站原则上按行政区域划分建设，也可按行业、重点产业等进行设立。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第三条 &nbsp; 指导站建设要遵循因地制宜、均衡布局、高效协同、科学规范的原则，坚持以市场需求为导向，集中优势资源建设，允许有条件的站点可配套独立办公场所，做好相关工作服务保障。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第四条 &nbsp; 指导站应配备必要的办公设施，做到统一标志认定、制度规范上墙、人员职责公示，网络设施符合日常办公需求。</p><p><strong> </strong></p><p><br></p><p><strong>第二章 组织架构</strong></p><p> </p><p><br></p><p style="text-indent: 2em;">第五条 &nbsp;指导站设站长、副站长、工作人员若干名，示范站站长、副站长由商标中心主任、副主任兼任，其他指导站站点站长、副站长由商标中心选举派任或合作共建单位相关领导负责人兼任。站长职责为推动政府部门及上级主管单位出台和完善品牌发展各项扶持政策，协调指导站与商标中心各部门、指导站各站点间关系；部署、指导、督促、检查指导站开展各项工作。副站长职责为协助站长制订、实施品牌发展规划；确定年度工作目标，制定计划，组织实施；指导、督促指导站工作人员、指导员开展各项工作。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第六条 &nbsp;指导站日常工作人员由合作共建单位相关责任人及其工作人员构成，其中专职工作人员不少于一名，辅助工作人员、指导专员可根据年度工作计划量按需配置。专职工作人员职责为收集企事业单位需求，接待回复有关咨询、做好相关记录、建立工作台账；协助申请人办理商标注册、续展、转让、许可使用等事务；提供商标信息、状态查询，商标申请预审咨询服务；做好商标数据库信息采集和录入，统计和上报工作；做好商标“商标咨询意见书”发放备案与台账记录；接收商标品牌侵权纠纷投诉，提供纠纷调解和维权指导，宣传商标法律法规及扶持政策；协助开展培训推广工作。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第七条 &nbsp; 指导站设立品牌指导专员，由商标中心选派商标辅助审查员或指导站合作共建单位工作人员组成；统一由商标中心负责组织培训、考核和颁发证书。品牌指导专员工作职责为开展商标宣讲，调研收集区域企业商标品牌信息，开展商标数据信息采集，开展商标事务指导，指导商标申请、规范使用、维权、品牌建设、策划指导，组织开展培训工作，协助专职工作人员撰写工作记录，季度及年度工作简报和总结等工作。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第八条 &nbsp;省保护中心对指导站的设立和运行进行宏观指导，商标中心组织实施对指导站的业务绩效考核及评审认定工作；经商标中心内部考核、评审并报省保护中心确定后，商标中心向合作共建单位发函对指导站设立、重建或撤销予以确认。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第九条 &nbsp;指导站创建及日常运行经费由商标中心统筹保障，争取部省项目立项，省、市级知识产权专项经费等为支撑，多渠道解决经费来源。</p><p> </p><p><br></p><p><strong>第三章 工作原则（要求）</strong></p><p><br></p><p> </p><p style="text-indent: 2em;">第十条 &nbsp;指导站工作开展要突出便民、高效、快捷、务实、专业的原则，及时响应行业协会及企业需求，为各类市场主体和行业协会提供便利化、高质量的商标品牌指导和咨询服务。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第十一条 &nbsp; 指导站要坚持以市场需求为导向，开展可行性论证，推动建立面向基于产业集群的区域品牌的商标品牌指导站点，完善培育机制，发挥集体商标、证明商标制度作用，打造特色鲜明、竞争力强、市场信誉好的产业集群品牌和区域品牌推动先进制造业、现代服务业等产业集群品牌商业效益扩大化。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第十二条 &nbsp; 指导站要充分吸收外部优势资源，吸纳行业协会、企业、高校院所、法律服务机构等单位的资深专家，共同提供业务指导和商标品牌培育帮扶服务，加强相关业务能力建设，加大业务培训力度，提高服务水平和效能。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第十三条 &nbsp; 指导站要加强与政府部门、基层服务组织的联系和沟通，建立相互协助机制，凝聚力量共同推进品牌建设工作。围绕地方经济和发展规划，围绕产业布局开展调查研究，提供工作建议，积极争取将商标品牌建设纳入政府整体发展规划，支撑地方政府部门加强产业集群区域商标品牌建设工作，促进商标品牌建设与产业发展深度融合。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第十四条 &nbsp; &nbsp;指导站要加大对驰名商标、知名商标、重点商标、地理标志品牌的培育和保护力度，推动商标品牌数据库建设，建立省、市、区级商标、地理标志品牌企业名录，要进一步拓展延伸服务范围，涵盖商标、专利和地理标志，提升自我服务能力，巩固服务效果。</p><p> </p><p><br></p><p><strong>第四章 工作内容</strong></p><p> </p><p><br></p><p style="text-indent: 2em;">第十五条 &nbsp; 商标品牌注册指导。指导站应充分发挥平台集聚效益，建立工作联系机制，帮助有需求的企业完善商标注册申请、布局、使用、转让许可、风险防范等管理制度，在商标注册、续展、运用、专用权保护等方面，提供参考信息和专家意见，引导企业对注册商标做到正确使用、有效保护。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第十六条 &nbsp; 商标品牌运用促进。指导站要强化商标品牌培育帮扶指导，引导企业加强商标品牌资产管理意识，强化商标使用导向，提升企业商标品牌保护和运用意识；加快研究完善高价值商标品牌评价标准制定，支持站点区域内企业开展高价值商标品牌培育与运用，提供商标质押融资、证券化、许可使用等商标品牌创建运用协调指导服务，拓宽企业融资渠道，促进企业知识产权价值转化运用。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第十七条 &nbsp; 商标品牌制度建设。指导站要持续做好站点内控制度建设，健全日常管理制度体系，厘清制度框架，充实和完善规章制度，建立联席会议、专题会议、联络人，信息收集（征集）、考核、保密、投诉处理等多项工作制度，梳理工作任务和业务流程，细化考核评审标准，明确岗位职责，压实责任主体，推动落实岗位责任制，确保各项工作专人负责、督办跟踪、运转高效，做好风险防范，有序推进指导站建设、运行和管理。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第十八条 &nbsp; 商标品牌规划编制。指导站要紧密结合《广东省知识产权保护和运用 “十四五” 规划》工作要求，全面深化推进我省商标品牌战略实施，高标准高要求引领我省指导站建设发展，高起点谋划指导站发展规划，为指导站建设推进提供工作指引；指导站要据实做好年度工作规划，明确年度总体目标和工作任务，确定总体实施方案及实施计划，做好经费预算编制，为年度工作开展提供强有力支撑。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第十九条 &nbsp; 商标品牌政策解读。指导站要及时组织开展国家、省级知识产权战略纲要、发展规划和政策文件的集体学习，坚持学懂弄通悟透做实；为相关企业解读政策文件出台的背景、工作任务及要求、主要内容、涉及范围、操作步骤、注意事项等，为企业答疑解惑，厘清思路，避开误区，引导企业正确理解文件内容，确保文件精神传达到位，推动后续工作开展落实落地。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第二十条 &nbsp; 商标品牌纠纷调解。指导站要推动建立商标品牌纠纷调解机制，完善商标品牌纠纷快速处理机制，针对企事业单位及市场需求主体特点和需求，开展企业商标品牌纠纷前置调解工作，提供商标品牌纠纷咨询和维权指引服务，指导企业进行商标维权，帮助搜集侵权线索和提出维权建议，必要时可发放《商标纠纷处理意见书》，有效提升企业竞争力和企业知识产权纠纷化解应对能力。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第二十一条 &nbsp; 商标品牌预警监测。指导站要逐步建立健全面向全省的商标前置预警监测机制，在各区各行业领域建立商标品牌重点保护名录，对高知名度商标开展商标预警监测，通过人工智能搜索、云计算、大数据分析等技术手段，为名录企业在多渠道进行商标侵权线索监控，形成监测及分析报告，为权利人及相关利害人发送《商标监测预警通知书》、《商标侵权抢注预警》等，为企业维权提供相关线索和专业意见支撑。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第二十二条 &nbsp; 商标品牌培训教育。指导站要认真做好全年知识产权培训计划，广泛收集企业的意见与建议，结合调研工作有针对性地开展品牌培训辅导服务，培训活动要契合主题、培训场数及服务企业覆盖范围要达到既定目标要求，培训成效要突出实际，切实提升企业商标培育注册、保护运用等方面的能力水平，提高商标申请率和注册成功率。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第二十三条 &nbsp; 商标品牌宣传推广。指导站应通过设置宣传栏、召开座谈会、动员会、宣传展览等各种方式，充分结合发挥微信公众号等自媒体平台的优势，丰富商标品牌指导站服务模式，构建和拓宽对外宣传媒体渠道，宣传商标品牌战略实施的重要意义，加强商标品牌法律法规知识的宣讲力度及展示商标品牌指导站的良好形象，努力营造社会关注品牌建设的良好氛围，增强市场主体商标品牌创建、运用和保护意识。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第二十四条 &nbsp; 商标品牌荣誉建设。指导站要注重荣誉建设带来的实际激励作用，要综合运用长效的激励手段和制定完善的激励制度，有序开展企业品牌建设、企业商标布局、企业商标运用发展指数、企业商标保护水平、指导站星级评价、指导站工作人员服务水平等的先进评优工作，支持企业参加“商标品牌节”等交易博览展会，通过表彰优秀和文化展览等宣传展示，助力企业打造知名、驰名商标，进一步增强企业荣誉感和服务认同，优化指导站服务供给效能和质量，提升指导站服务人员的积极性和企业参与度。</p><p style="text-indent: 2em;"> </p><p style="text-indent: 2em;"><br></p><p><strong>第五章 评审考核</strong></p><p> </p><p><br></p><p style="text-indent: 2em;">第二十五条 &nbsp; 省内各类知识产权企事业单位、创新主体、产业园区、行业协会、服务机构等在平等自愿的基础上，向商标中心提交合作共建申报材料。商标中心对指导站申报合作共建单位体提交的资料进行评审，必要时进行实地考察，综合考虑申报单位的具体条件、地域分布和行业布局等因素进行评定。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第二十六条 &nbsp; 经商标中心内部评审并报省知识产权保护中心确定名单后，商标中心向合作共建单位发函对指导站设立予以确认。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第二十七条 &nbsp; 商标中心对指导站定期开展考核，对考核结果为不达标的指导站，给予三个月的整改期限，期限后仍未达到要求的，上报请示省保护中心，有权终止合作共建单位相关合作协议。</p><p style="text-indent: 2em;"> </p><p style="text-indent: 2em;"> </p><p><strong>第六章 决议制度</strong></p><p> </p><p><br></p><p style="text-indent: 2em;">第二十八条 &nbsp; 指导站应建立联席会议制度和专题会议制度。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第二十九条 &nbsp; 联席会议可分年初、年中、年末召开，参加人员为监管及指导单位分管业务领导、各指导站站长、相关业务部门负责人、日常工作人员、指导专员等；同步邀请指导站设立所在区政府部门相关负责人、各区企业代表、行业协会负责人等。年初会议内容侧重确定新年度商标品牌发展规划和任务目标、制定实施计划；年中会议侧重汇总评估工作开展进度和通报任务目标完成情况；年末会议侧重总结指导站年度工作、通报考核等级站点、开展先进评优等工作。指导站联席会议一年不得少于三次，并应做好相应会议记录。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第三十条 &nbsp; 专题会议不定期召开，参加人员为各指导站站长、日常工作人员、指导专员及其他与会议议题汇报有关的人员，可邀请指导单位相关业务负责人、企业代表出席。会议主要内容为研究学习知识产权发展战略规划，解读知识产权政策；开展重要事项决议，布置和推进某项重点工作，汇报工作进度；研究商标品牌重要展会的组织实施；议定受理重点企业商标品牌纠纷调解、商标维权申请；开展企业约谈等。</p><p><br></p><p><br></p><p>第七章 工作纪律</p><p><br></p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第三十一条 &nbsp; 指导站工作人员及指导专员开展指导服务时，应遵守相关法律法规、严格按照广州商标审查协作中心工作纪律要求执行，态度端正，积极负责。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第三十二条 &nbsp; 指导站工作人员及指导专员正确履行工作职责，坚守工作岗位，不得擅离职守，服从工作安排，强化自律意识，坚守党性原则。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第三十三条 &nbsp; 指导站工作人员及指导专员应廉洁自律，自觉遵守指导站工作规定，严禁利用职务便利索要、收受或变相收受他人财物，不得利用工作之机谋私利。对利用职务之便从事违法乱纪的，移送司法部门处置。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第三十四条 &nbsp; 指导站工作人员及指导专员不得随意泄露企业涉密数据信息，签署保密协议的应严格遵守保密规定，妥善保管好企业文件资料，未获授权不得随意使用、修改、发布、转借等。</p><p> </p><p><br></p><p><strong>第八章 附 &nbsp;则</strong></p><p> </p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第三十五条 &nbsp; 本《规程》自发布之日起施行。</p><p style="text-indent: 2em;"><br></p><p style="text-indent: 2em;">第三十六条 &nbsp; 本《规程》由广州商标审查协作中心负责解释。</p><p> <br></p>`
        }
    ],
    service: [
        {
            articleId: 1,
            content:`<p style="text-align: left;"><span style="font-size: 29px;"><strong>服务规范</strong></span></p><p style="text-align: left;"><br></p><p style="text-align: left;"><strong>第一章 &nbsp;总 &nbsp;则</strong></p><p style="text-align: left;"><br></p><p style="text-align: left;"> </p><p style="text-indent: 2em; text-align: left;">第一条 &nbsp; 为推动构建便民利民的知识产权公共服务体系，加快推进知识产权公共服务标准化建设，全面深化商标品牌战略实施，加强商标品牌指导站建设，进一步提升指导站服务水平和能级，促进商标品牌指导站服务供给的规范化、标准化，特制定本规范。</p><p style="text-indent: 2em; text-align: left;"><br></p><p style="text-indent: 2em; text-align: left;">第二条 &nbsp; 本规范适用主体为由广州商标审查协作中心（以下简称审协中心）按规划自主设立或和其他各类知识产权公益性事业单位、行业协会、高校院所、知识产权服务机构等合作共建的商标品牌指导站。</p><p style="text-indent: 2em; text-align: left;"><br></p><p style="text-indent: 2em; text-align: left;">第三条 &nbsp; 本规范主要涵盖内容包括商标品牌指导站建设运营服务要求、服务内容、服务模式、服务流程、服务保障等。</p><p style="text-align: left;"><br></p><p style="text-align: left;"> </p><p style="text-align: left;"><strong>第二章 服务要求</strong></p><p style="text-align: left;"><br></p><p style="text-align: left;"> </p><p style="text-indent: 2em; text-align: left;">第四条 &nbsp; 商标品牌指导站应该明确并公示站点工作时间、联系人及联系方式，要实行首问责任制度，做到权责清晰，机制顺畅，及时有效地为市场主体和社会大众提供高质量的服务指导。</p><p style="text-indent: 2em; text-align: left;"><br></p><p style="text-indent: 2em; text-align: left;">第五条 &nbsp;商标品牌指导站工作人员在接待服务对象中，应做到礼貌热情、态度积极、着装整洁，仪表端庄、举止文明、言语规范、业务熟悉、办事高效、并做好相关咨询记录及服务工作台账。</p><p style="text-indent: 2em; text-align: left;"><br></p><p style="text-indent: 2em; text-align: left;">第六条 &nbsp; 商标品牌指导站工作人员在工作开展中应秉公办理各项业务，自觉遵守指导站工作制度要求，负有高度岗位责任感和社会责任意识，要以维护服务对象自身权益出发，立足其发展需要，为服务对象答疑解惑，助力市场主体纾困解难。</p><p style="text-indent: 2em; text-align: left;"><br></p><p style="text-indent: 2em; text-align: left;">第七条 &nbsp;商标品牌指导站在服务接待过程，应该做到高效便捷；在职责权限范围内的业务咨询，要做到一次性告知；在相关业务受理后，要在一定期限内办结，并做好相关工作记录及回访。</p><p style="text-indent: 2em; text-align: left;"><br></p><p style="text-indent: 2em; text-align: left;">第八条 &nbsp;商标品牌指导站应建立重大事项报告制度，面对上级单位交办的重要事项、紧急事项和突发重大任务等，要以站点第一责任人为紧急联系人，逐级向上层报告请示，特殊情况事项需提交至专席会议或联席会议研究。</p><p style="text-align: left;"><br></p><p style="text-align: left;"> </p><p style="text-align: left;"><strong>第三章 服务内容</strong></p><p style="text-align: left;"> </p><p style="text-align: left;"><br></p><p style="text-indent: 2em; text-align: left;">第九条 商标品牌指导站基础服务</p><p style="text-indent: 2em; text-align: left;">（一）商标品牌创建指导。包括企业商标品牌制度建设规划建议，提供商标设计、品牌策划、商标注册申请管理档案制定等方面的意见。</p><p style="text-indent: 2em; text-align: left;">（二）商标注册申请指导。包括商标申请注册、续展、转让、注销、变更、无效宣告、预审等业务的咨询和指导服务，产业集群品牌注册、集体商标、证明商标、地理标志的帮扶指导，支持企业开展海外商标布局，推广宣传马德里商标国际注册申请。</p><p style="text-indent: 2em; text-align: left;">（三）商标流程跟踪提示。根据企业商标注册申请规模及数量，指导企业加强商标状态管理，从商标注册申请提交、驳回、复审、初审公告、注册期限等全流程跟踪，防止核心商标注册申请被驳回，预防市场第三方恶意申请造成在先权力阻碍，帮助企业及时获取并了解商标注册申请最新进展，避免环节时效过期。</p><p style="text-indent: 2em; text-align: left;">（四）商标侵权纠纷。受理指导站所在辖区商标侵权咨询，开展维权指引，指导企业开展证据收集维护企业商标专用权，规范企业使用注册商标，抵御商标侵权风险。</p><p style="text-indent: 2em; text-align: left;">（五）商标运营管理。指导企业加强商标资产管理意识，注重企业商标品牌管理，支持站点区域内企业开展高价值商标品牌培育与运用，协调相关机构为企业开展无形资产评估，提供商标质押融资、证券化、许可使用等转化运用指导服务，助力企业盘活存量资产和优化企业资产结构。</p><p style="text-indent: 2em; text-align: left;">（六）商标宣传培训。包括商标品牌建设及知识产权相关的法律法规宣传、政策文件解读、深度案例分析、专题培训提升、专业技能拓展。</p><p style="text-indent: 2em; text-align: left;"><br></p><p style="text-indent: 2em; text-align: left;">第十条 &nbsp; 商标品牌指导站服务延伸</p><p style="text-indent: 2em; text-align: left;">（一）商标侵权判定。针对企事业单位所在行业、经营范围、申请商标类别，商标应用等方面，从应用商标主体本身的近似情况及商标。</p><p style="text-indent: 2em; text-align: left;">（二）商标预警提示。面向辖区内重点企业、需求企业、战略合作企业的驰名商标、著名商标、核心商标等、提供抢注监测预警、到期续展预警服务，出具《商标抢注预监测预警通知书》、《商标续展注册预警通知书》，为企业监测可能存在商标权力冲突申请注册情况并提供应对措施，维护企业商标合法权益；监测注册商标有效期情况，及时提醒企业在规定期限内办理商标续展手续，保障商标专用权稳定。</p><p style="text-indent: 2em; text-align: left;">（三）商标诊断分析。摸排企业商标注册申请及使用状况，排查企业注册商标使用管理风险及保护漏洞，出具企业商标诊断分析报告，指导企业建立健全商标注册应用管理机制、保护机制，进一步完善保护措施。</p><p style="text-indent: 2em; text-align: left;">（四）商标供需对接。协助企业开展商标数据信息检索，围绕企业商标布局和发展规划，分析企业需求，检索企业需求商标类型及商品服务类别，通过建立完善、可靠的供需对接机制和渠道，指导企业有序开展商标转让交易等，为企业在商标交易过程规避风险，保障好自身权益。</p><p style="text-indent: 2em; text-align: left;">（五）商标大数据分析。以商标大数据为基础，通过大数据分析分类，编制区域商标发展分析报告，为企业或地方政府部门制定重大战略提供意见参考，有效支撑地方政府部门加强商标品牌建设工作，争取将商标品牌建设纳入政府整体发展规划。（放入服务中心工作清单）</p><p style="text-indent: 2em; text-align: left;">（六）商标优势资源建设。通过调研统计，搭建辖区特色产业、优势企业资源数据库，指导具备一定规模优势的产业优势的园区、企业申报国家和省示范园区、省市级商标品牌示范企业等项目的申报工作，立足资源优势，争创优势品牌。</p><p style="text-align: left;"> </p><p style="text-align: left;"><br></p><p style="text-align: left;"><strong>第四章 服务模式及流程</strong></p><p style="text-align: left;"><br></p><p style="text-indent: 2em; text-align: left;"> </p><p style="text-indent: 2em; text-align: left;">第十一条 &nbsp; 商标品牌指导站根据实际情况采用专家授课、调研走访、考察学习、座谈交流、培训宣传、线上互动、信息推送、媒体发布等方式开展各项帮扶指导服务。</p><p style="text-indent: 2em; text-align: left;"><br></p><p style="text-indent: 2em; text-align: left;">第十二条 &nbsp; 商标品牌指导站依据各项业务工作开展实际情况，应切实完善并规范制定各业务全流程操作，正确引导工作人员按流程指引有序开展各项服务，明确各业务环节的具体要求和操作细则，对主要业务的通用流程进行梳理、归纳和总结，制定业务流程（见下图），进行规范化管理。</p><p style="text-align: center;"><img src="data:image/png;base64," alt="image.png" data-href="" style=""></p><p style="text-align: left;"> </p><p style="text-align: left;"><strong>第五章 服务保障</strong></p><p style="text-align: left;"><br></p><p style="text-align: left;"> </p><p style="text-indent: 2em; text-align: left;">第十三条 &nbsp; &nbsp;商标品牌指导站应设立在市场主体集聚、交通便利、配套设施相对完善、环境良好的场所，可依托各产业园区、特色小镇、基层组织、行业协会、高校院所、知识产权服务机构、知识产权工作站、服务站等平台独立建设或集成建设。</p><p style="text-indent: 2em; text-align: left;"><br></p><p style="text-indent: 2em; text-align: left;">第十四条 &nbsp; 商标品牌指导站应配置固定的办公场所，场地规模及布局应满足日常接待、会议、宣传、培训、指导等活动开展需要。</p><p style="text-indent: 2em; text-align: left;"><br></p><p style="text-indent: 2em; text-align: left;">第十五条 &nbsp; 商标品牌指导站应建立和完善日常管理制度体系，包括日常咨询、接待、培训、会议、指导、投诉处理、争议纠纷等日常管理制度并严格按要求严格执行。</p><p style="text-indent: 2em; text-align: left;"><br></p><p style="text-indent: 2em; text-align: left;">第十六条 &nbsp; 商标品牌指导站应根据年度工作计划及工作量，应设立站点责任人及相应数量的专职工作人员，如有必要，可另外配备辅助或临时工作人员。</p><p style="text-indent: 2em; text-align: left;"><br></p><p style="text-indent: 2em; text-align: left;">第十七条 &nbsp; 商标品牌指导站应完善外部合作、沟通及管理机制，吸纳社会优势资源，联合第三方资源服务机构和行业专家，共同为企业提供专业、规范的服务指导。</p><p style="text-align: left;"> </p><p style="text-align: left;"><br></p><p style="text-align: left;"><strong>第六章 服务评价与改进</strong></p><p style="text-align: left;"><br></p><p style="text-align: left;"> </p><p style="text-indent: 2em; text-align: left;">第十八条 &nbsp; 商标品牌指导站要逐步建立和完善服务考评体系，结合工作台账记录，定期对服务内容、服务效果、服务态度、服务方式、服务对象等方面进行检查和综合评价。</p><p style="text-indent: 2em; text-align: left;"><br></p><p style="text-indent: 2em; text-align: left;">第十九条 &nbsp; 商标品牌指导站要围绕考评服务体系建设，综合运用满意度调查、信访调查、意见箱、回访登记等方式及时掌握服务对象和市场主体的意见反馈和投诉情况，做好受理记录、原因调查、分析查找、总结归纳、服务提升等工作。</p><p style="text-indent: 2em; text-align: left;"><br></p><p style="text-indent: 2em; text-align: left;">第二十条 &nbsp; 商标品牌指导站应该持续改进工作机制、深化工作模式，根据自查自纠、投诉建议、公开征集、市场反馈、考核评价等制定改进措施，跟踪改进情况，汇总工作成果。</p><p style="text-align: left;"> </p><p style="text-align: left;"><br></p><p style="text-align: left;"><strong>第七章 附 &nbsp;则</strong></p><p style="text-align: left;"><br></p><p style="text-align: left;"> </p><p style="text-indent: 2em; text-align: left;">第二十一条 &nbsp; 本《规程》自发布之日起施行。</p><p style="text-indent: 2em; text-align: left;"><br></p><p style="text-indent: 2em; text-align: left;">第二十二条 &nbsp; 本《规程》由广州商标审查协作中心负责解释。</p><p style="text-align: left;"><br></p><p style="text-align: left;"><br></p>`
        }
    ],
}